html {
    min-width: 300px;
}

body {
    margin: 0;
    min-width: 300px;
    background-color: rgb(0, 0, 30);
    background: linear-gradient(135deg, rgb(0, 0, 30), rgb(0, 0, 30));
    font-family: Arial, sans-serif;
}

.extremidades {
    width: 100%;
    min-width: 300px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    background-color: #F2F2F2;
    color: black;
    font-family: Arial, Helvetica, sans-serif;

}
/*MAIN*/


/*MENU*/
main {
    margin-top: 20vh;
    display: flex;
    width: 100%;
    min-height: 65vh;
    flex-direction: row;
    justify-content: center;
}


/*CONTEUDO*/

#conteudo{
    margin-top: 75px;
    display: flex;
    width: 91%;
    min-height: 60vh;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.descricao{
    background-color:  #ccc8c4;
    width: 80%;
    min-height: 35vh;
    border-radius: 2%;
    margin: 10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.img-descricao{
    padding: 20px;
    max-width: 35%;
    min-width: 35vh;
}


.texto-descricao{
    max-width: 50%;
    font-family: Arial, Helvetica, sans-serif;
    color: black;
    text-align: center;
    padding: 15px;
}




/*RODAPÃ‰*/

footer {
    /*position: fixed;
    bottom: 0;*/
    justify-content: center;
    height: 15vh;
    width: 100%;
}

#logo-furb {
    padding: 6px;
    width: 80px;
    height: auto;
}

#infos {
    font-size: 0.8em;
    padding-right: 2%;
    text-align: center;
}


